// 
import Container from 'react-bootstrap/Container';
import scorepic from '../assets/images/score.png'
import { useEffect, useState } from 'react';
import Swal from 'sweetalert2';
import { useNavigate } from 'react-router-dom';
import { Card, Col, Row } from 'react-bootstrap';


export default function Aftertestpage() {
  const [q, setQ] = useState(0);    
  const [result, setResult] = useState(true);
  const [afterscore,setAfterscore] = useState(0);
  const navigate = useNavigate();

  useEffect(() => {
    switch (q) {
        case 0:
            setResult({
                'ques': '1-10 ',
                'a': '',
                'b': '',
                'c': '',
                'd': '',
            });
            break;
        case 1:
            setResult({
                'ques': '2-10 ',
                'a': '',
                'b': '',
                'c': '',
                'd': '',
            });
            break;
        case 2:
            setResult({
                'ques': '3-10 ',
                'a': '',
                'b': '',
                'c': '',
                'd': '',
            });
            break;
        case 3:
            setResult({
                'ques': '4-10 ',
                'a': '',
                'b': '',
                'c': '',
                'd': '',
            });
            break;
        case 4:
            setResult({
                'ques': '5-10 ',
                'a': '',
                'b': '',
                'c': '',
                'd': '',
            });
            break;
        case 5:
            setResult({
                'ques': '6-10 ',
                'a': '',
                'b': '',
                'c': '',
                'd': '',
            });
            break;
        case 6:
            setResult({
                'ques': '7-10 ',
                'a': '',
                'b': '',
                'c': '',
                'd': '',
            });
            break;
        case 7:
            setResult({
                'ques': '8-10 ',
                'a': '',
                'b': '',
                'c': '',
                'd': '',
            });
            break;
        case 8:
            setResult({
                'ques': '9-10 ',
                'a': '',
                'b': '',
                'c': '',
                'd': '',
            });
            break;
        case 9:
            setResult({
                'ques': '10-10 ',
                'a': '',
                'b': '',
                'c': '',
                'd': '',
            });
            break;    
        default:
            break;
    }
  }, [q]);

  function handleAns(option) {
    return () => {
        switch (q) {
            case 0:
                if (option === 'a') {
                    Swal.fire('Correct', '', 'success');
                    setQ(q + 1);
                    setAfterscore(afterscore + 1);
                } else {
                    Swal.fire('Mistake', '(A)', 'error');
                    setQ(q + 1);
                } 
                break;
            case 1:
                if (option === 'a') {
                    Swal.fire('Correct', '', 'success');
                    setQ(q + 1);
                    setAfterscore(afterscore + 1);
                } else {
                    Swal.fire('Mistake', '(A)', 'error');
                    setQ(q + 1);
                } 
                break;
            case 2:
                if (option === 'a') {
                    Swal.fire('Correct', '', 'success');
                    setQ(q + 1);
                    setAfterscore(afterscore + 1);
                } else {
                    Swal.fire('Mistake', '(A)', 'error');
                    setQ(q + 1);
                } 
                break;
            case 3:
                if (option === 'b') {
                    Swal.fire('Correct', '', 'success');
                    setQ(q + 1);
                    setAfterscore(afterscore + 1);
                } else {
                    Swal.fire('Mistake', '(B)', 'error');
                    setQ(q + 1);
                } 
                break;
            case 4:
                if (option === 'b') {
                        Swal.fire('Correct', '', 'success');
                        setQ(q + 1);
                        setAfterscore(afterscore + 1);
                } else {
                    Swal.fire('Mistake', '(B)', 'error');
                    setQ(q + 1);
                } 
                break;
            case 5:
                if (option === 'c') {
                        Swal.fire('Correct', '', 'success');
                        setQ(q + 1);
                        setAfterscore(afterscore + 1);
                } else {
                    Swal.fire('Mistake', '(C)', 'error');
                    setQ(q + 1);
                } 
                break;
            case 6:
                if (option === 'a') {
                        Swal.fire('Correct', '', 'success');
                        setQ(q + 1);
                        setAfterscore(afterscore + 1);
                } else {
                    Swal.fire('Mistake', '(A)', 'error');
                    setQ(q + 1);
                } 
                break;
            case 7:
                if (option === 'c') {
                        Swal.fire('Correct', '', 'success');
                        setQ(q + 1);
                        setAfterscore(afterscore + 1);
                } else {
                    Swal.fire('Mistake', '(C)', 'error');
                    setQ(q + 1);
                } 
                break;
            case 8:
                if (option === 'a') {
                        Swal.fire('Correct', '', 'success');
                        setQ(q + 1);
                        setAfterscore(afterscore + 1);
                } else {
                    Swal.fire('Mistake', '(A)', 'error');
                    setQ(q + 1);
                } 
                break;
            case 9:
                if (option === 'b') {
                    Swal.fire('Correct', '', 'success').then(() => {
                        Swal.fire({
                            text: ':' + (afterscore+1) + '/10',
                            imageUrl: scorepic,
                        })
                        navigate('/naildisease');
                    });
                } else {
                    Swal.fire('Mistake', '(B)', 'error').then(()=>{
                        Swal.fire({
                            text: ':' + afterscore + '/10',
                            imageUrl: scorepic,
                        })
                        navigate('/naildisease');
                    });
                } 
                break;
            default:
                break;
        }
    };
  }

  return (
    <section id="teams" className="block blog-block">
      <Container fluid>
        <Row>
            <Col sm={12} >
                <div className='holder' >
                    <Card>
                        <div className="title-holder">
                        <h2><b></b></h2>
                        </div>
                        <div className='testblock'>
                            <h3>{result.ques}</h3>
                            <a className="btn btn-answer mb-2" onClick={handleAns('a')}>(A) {result.a}</a>
                            <br/>
                            <a className="btn btn-answer mb-2" onClick={handleAns('b')}>(B) {result.b}</a>
                            <br/>
                            <a className="btn btn-answer mb-2" onClick={handleAns('c')}>(C) {result.c}</a>
                            <br/>
                            <a className="btn btn-answer mb-2" onClick={handleAns('d')}>(D) {result.d}</a>
                        </div>
                    </Card>
                </div>
                
            </Col>

        </Row>
      </Container>
    </section>
  );
}

